.logo {
  height: 20vmin;
  pointer-events: none;
}

.header {
  border-style: double;
  border-width: 0px 0px 3px 0px;
  border-color: rgb(94, 54, 54);
  font-size: calc(16px + 1vmin);
  text-shadow: 8px 5px 8px rgb(154, 105, 105);
  padding-bottom: 5px;
}

.subheader {
  font-size: calc(10px + 1vmin);
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 0.03em;
  margin: 10px;
  color: rgb(149, 103, 84);
}

.textBlock {
  text-indent: 20px;
  margin-left: 10px;
}

.unorderedList {
  list-style-type: '- ';
  text-indent: 0px;
  padding-left: 20px;
}

.footer {
  border-style: solid;
  border-width: 2px 0px 0px 0px;
  border-color: #eb9767;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: inset 0px 4px 5px -5px #a74e1b;
}

.link {
  &:link {
    color: #a74e1b;
  }

  &:visited {
    color: #eb9767;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .logo {
    animation: logo-spin infinite 20s linear;
  }
}

@keyframes logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
